.home {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
}

.home__content {
  width: 35vw;
  margin-top: -10%;
  transform: translateX(-150vw);
  transition: 2.5s;
}

.swiper-slide-active .home__content {
  transform: translateX(0);
  transition: transform 1s cubic-bezier(0.1, 0.39, 0, 1.12) 0s;
}

.home_content-title {
  font-style: normal;
  font-weight: bold;
  font-size: 6vh;
  text-transform: uppercase;
}

.home_content-title-top {
  color: #000;
  background-color: #fff;
  padding: 4px 9px 0px;
}

.home_content-title-bottom {
  font-size: 5vh;
}

.home_content-box {
  display: flex;
  margin-top: 10px;

  & li {
    margin-right: 17px;
    font-weight: bold;
    font-size: 1.8vh;
    text-transform: uppercase;
    display: flex;
    align-items: center;

    &::before {
      content: "";
      width: 5px;
      height: 5px;
      background: #e35d14;
      margin-right: 6px;
      border-radius: 100%;
    }
  }
}

.animation-wrapper {
  position: absolute;
  top: 50%;
  left: 60%;
  width: 50vh;
  height: 50vh;
  min-height: 300px;
  min-height: 300px;
  transform: translate(-50%, -50%);
}

.sphere-animation {
  width: 100%;
  height: 100%;
}

.sphere path {
  fill: url(#sphereGradient);
  stroke-width: 1px;
  stroke: rgba(80,80,80,.35);
  backface-visibility: hidden;
}
