.small-page {
  position: relative;
  padding: 150px 0 0;
  color: #fff;
  background: #000;

  & a {
    color: #fff;
  }
}

.small-page .contacts__box {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.small-page .contacts__title {
  flex: 1 1 100%;
}

.small-page .why-form {
  width: 50%;
}
