.pages {
  background: #fff;
}

.pages__main {
  color: #000;
}

.project__slider {
  display: flex;
  align-items: flex-start;
}

.project__slide-active {
  width: 700px;
}

.categoria {
  display: inline-block;
  position: absolute;
  top: 7%;
  left: 20%;
  color: #fff;
  font-weight: 900;
  font-size: 17px;
  text-transform: uppercase;
  padding: 9px 15px 10px;
  z-index: 1;

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    background: $prime-color;
    position: absolute;
    top: 0;
    left: 0;
    transform: skew(-10deg);
    z-index: -1;
  }
}

.project__slider-wrap {
  position: relative;
  max-width: 37%;
  margin-left: auto;
}

.project__slide {
  width: 100% !important;
}

.project__slider .swiper-slide-shadow {
  background: none;
}

.project__slider .swiper-slide img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.project__slider-text {
  width: 45%;
  margin-left: auto;
  margin-top: -200px;
  margin-bottom: 75px;

  & h1 {
    font-weight: 900;
    font-size: 24px;
    margin-bottom: 18px;
  }
}

.project__elements {
  margin-bottom: 75px;

  & h2 {
    font-size: 24px;
    text-align: center;
    margin-bottom: 36px;
  }
}

.pages__list {
  max-width: 980px;
  margin: 0 auto !important;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  & li {
    width: 47%;
    padding-left: 25px;
    position: relative;
    margin-bottom: 20px;

    &::after {
      content: "";
      width: 11px;
      height: 11px;
      position: absolute;
      top: 5px;
      left: 0;
      background: $prime-color;
      border-radius: 50%;
    }
  }
}

.projects__pages {
  margin-bottom: 75px;

  & h2 {
    font-size: 24px;
    text-align: center;
    margin-bottom: 36px;
  }

  .swiper-horizontal>.swiper-scrollbar {
    position: static;
    margin-top: 80px;
  }

  .swiper-scrollbar-drag {
    background: rgb(227 93 20);
  }
}

.project__page-slide p {
  font-weight: bold;
  font-size: 20px;
}

.project__order {
  background: url('../img/order.png') no-repeat right center/auto 100%;
}

.pages__footer {
  padding: 40px 0;
}

.pages__footer-project {
  padding: 240px 0 40px;
}

.pages__footer-contacts {
  display: flex;
  flex-wrap: wrap;
  max-width: 525px;
  justify-content: space-between;
  align-items: flex-start;
}


.pages__footer-contacts .contacts__item a,
.pages__footer-contacts .contacts__item.adres {
  font-size: 24px;
  margin-bottom: 0;
  line-height: 1.2;
  color: #000;

  &::before {
    width: 20px;
    height: 20px;
    margin-right: 15px;
  }
}

.pages__footer-contacts .contacts__item {
  margin-bottom: 10px;
}

.pages__footer-contacts .phone,
.pages__footer-contacts .email,
.pages__footer-contacts .adres {
  opacity: 1;
  transform: translateY(-50px);
  transition: transform 0.8s cubic-bezier(0.1, 0.39, 0, 1.12);
}

.pages__footer-contacts .phone a::before {
  background-image: url('../img/call.svg');
}

.pages__footer-contacts .email a::before {
  background-image: url('../img/ios-mail.svg');
}

.pages__footer-contacts .adres::before {
  background-image: url('../img/compass.svg');
}

.pages__footer-text {
  display: flex;
  justify-content: space-between;
}

.pages__title {
  font-size: 7vh;
  margin-bottom: 10px;
}

.projects__text {
  max-width: 800px;
}

.tabs {
  margin: 25px 0;
}

.tab {
  font-style: italic;
  font-weight: 900;
  font-size: 24px;
  margin-right: 20px;
  text-decoration: none;
  transition: 0.8s;

  &:hover,
  &:focus,
  &.active {
    -webkit-text-stroke: 1px $prime-color;
    -webkit-text-fill-color: transparent;
  }
}

.projects__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

.projects__list .portfolio__item p {
  text-align: left;
  max-width: 80%;
  margin: 10px auto 0;
}

.project__link {
  display: inline-block;
  width: 80%;
  text-align: center;
  padding: 10px;
  background: #000;
  color: #fff;
  text-decoration: none;
  font-style: italic;
  font-weight: 900;
  font-size: 2.6vh;
  text-transform: uppercase;
  margin-top: 30px;
  transform: translateX(-100px);
  opacity: 0;

  &:hover,
  &:focus {
    color: #000;
    background: $prime-color;
  }
}

.projects__list .portfolio__item {
  position: relative;
  transition: 0.8s;
  min-width: 300px;
}

.projects__list .portfolio__item::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(0);
  border: 7px double grey;
  z-index: -1;
  transition: 0.7s;
}

.projects__list .portfolio__item:hover,
.projects__list .portfolio__item:focus {
  & .project__link {
    transform: translateX(20px);
    opacity: 1;
  }

  &::after {
    transform: translate(-50%, -50%) scale(1) skew(-10deg);
  }
}

.projects__list .portfolio__catigoria {
  color: #fff;
}

.projects__button {
  text-align: center;
  margin: 5vh 0 10vh;

  & a {
    font-style: italic;
    font-weight: 900;
    font-size: 7vh;
    line-height: 1.2;
    -webkit-text-stroke: 1px $prime-color;
    -webkit-text-fill-color: transparent;
    text-decoration: none;
    text-transform: uppercase;

    &:hover,
    &:focus {
      -webkit-text-fill-color: $prime-color;
    }
  }
}

@media (max-width: 1380px) {
  .pages .language {
    flex-direction: row;
    top: 175px;
    width: 135px;
    transform: none;
    width: 65px;
    justify-content: space-between;
  }

  .pages .language-icon {
    width: 2px;
    height: 28px;
    order: 2;
  }

  .pages .language-items {
    margin-top: 0;
  }

  .pages__header {
    padding-bottom: 70px;
  }

  .project__slider-wrap {
    margin-left: 0;
  }
}

@media (max-width: 1200px) {
  .project__slider-text {
    width: 100%;
    margin-top: 0;
    margin-bottom: 75px;
  }

  .project__slide-active {
    width: 50%;
  }

  .project__slider-wrap {
    max-width: 45%;
  }
}

@media (max-width: 820px) {
  .project__slide-active {
    display: none;
  }

  .project__slider-wrap  {
    max-width: 90%;
  }

  .back {
    font-size: 34px;
  }
}

@media (max-width: $mobile-width) {

  .pages .header__phone {
    background-image: url('../img/call-black.svg');
  }

  .pages .header__burger span {
    background: #000;
  }

  .back {
    font-size: 26px;
    top: 115px;
  }

  .pages .language {
    position: static;
  }

  .project__slider-text {
    margin-bottom: 40px;
  }

  .pages__list li {
    width: 100%;

    &::after {
      top: 10px;
    }
  }

  .projects__pages .swiper-horizontal>.swiper-scrollbar {
    margin-top: 41px;
  }

  .project__order {
    background: none;
  }

  .pages__footer-contacts .contacts__item a,
  .pages__footer-contacts .contacts__item.adres {
    font-size: 19px;
  }

  .pages__footer-contacts,
  .pages__footer-text {
    justify-content: space-evenly;
    text-align: center;
  }

  .pages__footer-text {
    flex-wrap: wrap;
  }

  .pages__title {
    font-size: 44px;
  }

  .projects__button a {
    font-size: 36px;
  }
}
